import { NavLink } from "react-router-dom";

const NavBar = () => {
  return (
    <header className="font-body w-full mx-auto">
      <div className="align-middle mx-auto container flex justify-between">
        <nav className="flex">
          <div className="font-title justify">
            <NavLink
              to="/"
              exact
              activeClassName="text-black"
              className="float-left items-center py-6 px-3 mr-4 text-black hover:text-gray-500 font-bold tracking-widest"
            >
              <div>
                <h1 className="text-center text-5xl"> Janelle Knight </h1>
                <h3 style={{ letterSpacing: 15 }} className="object-cover text-center text-2xl"> stallout </h3>
              </div>
            </NavLink>
          </div>
          <div className="h-full float-right flex justify">
            <NavLink
              to="/portfolio"
              style={{textUnderlineOffset: 3}}
              activeClassName="text-black underline"
              className="inline-flex items-center py-6 px-3 mr-6 rounded text-black hover:text-gray-500 text-xl tracking-widest"
            >
              Portfolio
            </NavLink>
            <NavLink
              to="/about"
              style={{textUnderlineOffset: 3}}
              activeClassName="text-black underline"
              className="inline-flex items-center py-3 px-3 mr-6 rounded text-black hover:text-gray-500 text-xl tracking-widest"
            >
              About
            </NavLink>
            <NavLink
              to="/contact"
              style={{textUnderlineOffset: 3}}
              activeClassName="text-black underline"
              className="inline-flex items-center py-3 px-3 mr-6 rounded text-black hover:text-gray-500 text-xl tracking-widest"
            >
              Contact
            </NavLink>
          </div>
        </nav>
      </div>
    </header>
  );
};

export default NavBar;
