import React from 'react'
import splash from "../../images/Elm Splash.png"
import mainimage from "../../images/AGDII Portfolio 01.png"
import port2 from "../../images/AGDII Portfolio 02.png"
import port3 from "../../images/AGDII Portfolio 03.png"
import port4 from "../../images/AGDII Portfolio 04.png"
import port5 from "../../images/AGDII Portfolio 05.png"
import port6 from "../../images/AGDII Portfolio 06.png"
import port8 from "../../images/AGDII Portfolio 08.png"
import uifull from "../../images/IMG_0241.JPG"
import uiempty from "../../images/IMG_0243.JPG"
import menu from "../../images/mock up 02.png"
import menu2 from "../../images/mock up 03.png"
import '../../cols.css'
import ImageGrid from '../util/ImageGrid'

const Elm = () => {
    const grid1 = [mainimage,   splash,     port2,
                   port3,       port4,      port5,
                   port6,       port8,      uifull,
                   uiempty,     menu,       menu2]

    return (
        <div className="flex flex-wrap ">
            <ImageGrid images={grid1} numCols={2} />
            <ImageGrid images={grid1} />
        </div>
    )
}

export default Elm
