import React from "react";
import "../../darken.css";
import mainimage from "../../images/AGDII Portfolio 01.png";
import { NavLink } from "react-router-dom";
import ProjectImage from "../util/ProjectImage";

const Portfolio = () => {
  return (
    <div className="bg-red-100">
      <div className="w-1/2 bg-black-900 text-center">
        <NavLink to="/elm">
            <ProjectImage project_image={mainimage} />
        </NavLink>
      </div>
    </div>
  );
};

export default Portfolio;
