import { useState } from "react";
import "../../slideshow.css";
import Slide from "./Slide";

const Slideshow = ({ images }) => {
  const slides = [];
  images.map((i, j) => ( 
    slides.push(<Slide image={i} />)
  ))
  const [slideIndex, setIndex] = useState(0);

  function nextSlide() {
    if(slideIndex + 1 >= slides.length - 1) {
      setIndex(0);
    } else {
      setIndex(slideIndex + 1);
    }
    console.log(slideIndex);
  }

  function prevSlide() {
    if(slideIndex <= 0) {
      setIndex(slides.length - 1);
    } else {
      setIndex(slideIndex - 1);
    }
  }

  function goTo(n) {
    setIndex(n);
  }

  return (
    <div onLoad={setTimeout(() => {
      nextSlide()
    }, 2000)}>
      <div className="h-4/5 w-4/5 p-3 mx-auto">
        {slides[slideIndex]}
      </div>
      <div className="h-1/5 text-2xl" style={{textAlign: "center"}}>
        <button className="mr-3" onClick={prevSlide}>&#60;</button>
        {images.map((i, j) => (
            <span className="dot" style={ (slideIndex === j) ? {backgroundColor:"#000"} : {backgroundColor:"#bbb"} } onClick={() => goTo(j)}></span>
        ))}
        <button className="ml-3" onClick={nextSlide}>&#62;</button>
      </div>
    </div>
  );
}; 

export default Slideshow;
