import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTwitter } from "@fortawesome/free-brands-svg-icons";
import { faInstagram } from "@fortawesome/free-brands-svg-icons";
import { faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { faArtstation } from "@fortawesome/free-brands-svg-icons";
import janelleImage from "../../images/janelleportfolio.jpg";

const About = () => {
  return (
    <div className="container justify justify-center text-center mx-auto h-full mb-auto">
      <div className="font-body text-xl mx-auto container w-1/2">
        <img
          className="mx-auto rounded-full border w-1/3 h-1/5 border-gray-100 shadow-sm"
          src={janelleImage}
        />
        <br />
        <h1 className="text-7xl">
            Janelle Knight
        </h1>
        <p className="text-3xl">stallout9@gmail.com</p>
        <br />
        <p>Hello, I am Janelle!  I am a fourth year undergraduate student
            studying Interactive Media and Game Development in Massachusetts!
        </p>
        <br />
        <p className="text-left">
            I am currently working on a verticle slice game for FIRST Robitics. I am
            passionate about designing characters and building worlds and I have 7+ years
            working as a freeland artist as well as 3+ years working on games and related
            projects in small teams.  I am looking forward to working on more fun projects
             and with all kinds of people!
        </p>
      </div>
      <div className="mx-auto w-1/2">
        <br />
        <h1 className="font-title text-6xl">
          <a target="__blank" href="https://www.artstation.com/stallout">
            <FontAwesomeIcon icon={faArtstation} />{" "}
          </a>
          <a
            target="__blank"
            href="https://www.linkedin.com/in/janelle-knight/"
          >
            <FontAwesomeIcon icon={faLinkedin} />{" "}
          </a>
          <a target="__blank" href="https://instagram.com/stallout.art">
            <FontAwesomeIcon icon={faInstagram} />{" "}
          </a>
          <a target="__blank" href="https://twitter.com/stallout_art">
            <FontAwesomeIcon icon={faTwitter} />{" "}
          </a>
        </h1>
      </div>
    </div>
  );
};

export default About;
